//import React from 'react';
import './Header.css';
import FilterSidebar from './FilterSidebar';
import { Link } from 'react-router-dom';

function Header({ onOpenPopup, onSearch, onFilterChange, onCategorySelect, isLoggedIn, userRole, onShowFavorites, onOpenAdminPanel, filters }) {
  const handleInputChange = (event) => {
    onSearch(event.target.value);
  };
  
  return (
    <header className="header">
      <div className="header-content">
        <Link to="/" className="home-button"></Link>
        <input
          type="text"
          placeholder="  "
          onChange={handleInputChange}
        />
        {isLoggedIn && userRole === 'user' && (
          <button 
            className="favorites-button"
            onClick={onShowFavorites}
          >
            
          </button>
        )}
        {isLoggedIn && userRole === 'admin' && (
          <button 
            className="admin-button"
            onClick={onOpenAdminPanel}
          >
            
          </button>
        )}
        {isLoggedIn ? (
          <div className="user-actions">
            <button className="logout-button" onClick={onOpenPopup}> </button>
          </div>
        ) : (
          <button className="login-button" onClick={onOpenPopup}></button>
        )}
      </div>
      <div className="furniture-categories">
        <Link to="/category/  ">  </Link>
        <Link to="/category/  ">  </Link>
        <Link to="/category/  ">  </Link>
        <Link to="/category/  ">  </Link>
        <Link to="/category/  ">  </Link>
        <Link to="/category/ "> </Link>
      </div>
      <FilterSidebar 
        onFilterChange={onFilterChange}
        initialFilters={filters}
        onCategorySelect={onCategorySelect}
      />
    </header>
  );
}

export default Header;